import React, { useState, useEffect } from 'react';
import { VendaFinal, CreateVendaFinalRequest, TipoPagamento, StatusVendaFinal } from '@/services/api/venda.service';
import { gatewayService, Gateway } from '@/services/api/gateway.service';
import { conexaoService, Conexao } from '@/services/api/conexao.service';
import Button from '@/components/ui/Button';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Select, { SelectOption } from '@/components/ui/Select';
import { formatCurrency, formatCurrencyInput, parseCurrencyInput } from '@/utils/format';
import { toast } from 'react-hot-toast';
import DatePicker from '../form/date-picker';

type VendaFinalFormProps = {
  vendaFinal?: VendaFinal | null;
  vendaId: number;
  onSubmit: (data: CreateVendaFinalRequest) => Promise<void>;
  onCancel?: () => void;
};

const tipoOptions: SelectOption[] = [
  { value: 'PIX', label: 'PIX' },
  { value: 'BOLETO', label: 'Boleto' }
];

const statusOptions: SelectOption[] = [
  { value: 'PENDING', label: 'Pendente' },
  { value: 'APPROVED', label: 'Aprovado' },
  { value: 'CANCELLED', label: 'Cancelado' }
];

const VendaFinalForm: React.FC<VendaFinalFormProps> = ({
  vendaFinal,
  vendaId,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState<CreateVendaFinalRequest>({
    valor: Number(vendaFinal?.valor) || 0,
    status: vendaFinal?.status || 'PENDING',
    type: vendaFinal?.type || 'PIX',
    vencimento: vendaFinal?.vencimento ? new Date(vendaFinal.vencimento) : new Date(),
    dataPagamento: vendaFinal?.dataPagamento ? new Date(vendaFinal.dataPagamento) : undefined,
    vendaId: vendaId,
    id_payment: vendaFinal?.id_payment || '',
    copiacola: vendaFinal?.copiacola || '',
    qrcode: vendaFinal?.qrcode || '',
    codigobarra: vendaFinal?.codigobarra || '',
    gatewayId: vendaFinal?.gatewayId || undefined,
    conexaoId: vendaFinal?.conexaoId || undefined
  });

  // Estado para gateways e conexões
  const [gateways, setGateways] = useState<Gateway[]>([]);
  const [conexoes, setConexoes] = useState<Conexao[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [valorFormatado, setValorFormatado] = useState(
    vendaFinal?.valor ? formatCurrency(vendaFinal.valor).replace('R$', '').trim() : ''
  );

  // Carregar gateways e conexões
  useEffect(() => {
    const loadData = async () => {
      try {
        setIsLoading(true);
        const [gatewayRes, conexaoRes] = await Promise.all([
          gatewayService.list({ limit: 100 }),
          conexaoService.list({ limit: 100 })
        ]);
        setGateways(gatewayRes.data || []);
        setConexoes(conexaoRes.data || []);
      } catch (error) {
        console.error('Erro ao carregar dados:', error);
        toast.error('Erro ao carregar dados');
      } finally {
        setIsLoading(false);
      }
    };

    loadData();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.valor || formData.valor <= 0) {
      toast.error('Valor deve ser maior que zero');
      return;
    }

    if (!formData.vencimento) {
      toast.error('Data de vencimento é obrigatória');
      return;
    }

    try {
      setIsSubmitting(true);
      await onSubmit(formData);
    } catch (error) {
      console.error('Erro ao salvar venda final:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <Label htmlFor="valor">Valor *</Label>
          <div className="relative">
            <span className="absolute z-1 left-3 top-1/2 -translate-y-1/2 text-gray-500 dark:text-gray-300">
              R$
            </span>
            <Input
              id="valor"
              type="text"
              className="pl-9"
              value={valorFormatado}
              onChange={(e) => {
                const input = e.target.value.replace(/\D/g, '');
                if (input === '') {
                  setValorFormatado('');
                  setFormData({ ...formData, valor: 0 });
                  return;
                }

                const formatted = formatCurrencyInput(input);
                setValorFormatado(formatted);
                const parsedValue = parseCurrencyInput(input);
                console.log('Parsed Value:', parsedValue);
                setFormData({ ...formData, valor: parsedValue });
              }}
              placeholder="0,00"
              required
            />
          </div>
        </div>

        <div>
          <Label htmlFor="status">Status *</Label>
          <Select
            id="status"
            value={statusOptions.find(option => option.value === formData.status)}
            onChange={(option) => setFormData({ ...formData, status: (option as SelectOption).value as StatusVendaFinal })}
            options={statusOptions}
            placeholder="Selecione o status"
            required
          />
        </div>

        <div>
          <Label htmlFor="type">Tipo de Pagamento *</Label>
          <Select
            id="type"
            value={tipoOptions.find(option => option.value === formData.type)}
            onChange={(option) => setFormData({ ...formData, type: (option as SelectOption).value as TipoPagamento })}
            options={tipoOptions}
            placeholder="Selecione o tipo"
            required
          />
        </div>

        <div>
          <Label htmlFor="vencimento">Data de Vencimento *</Label>
          {/* <Input
            id="vencimento"
            type="date"
            value={formData.vencimento}
            onChange={(e) => setFormData({ ...formData, vencimento: e.target.value })}
            required
          /> */}
                    <DatePicker
                      id="data"
                      onChange={(selectedDates: Date[]) => {
                        if (selectedDates[0]) {
                          setFormData({
                            ...formData,
                            vencimento: selectedDates[0]
                          });
                        }
                      }}
                      defaultDate={new Date(formData.vencimento || new Date())}
                    />
        </div>

        <div>
          <Label htmlFor="dataPagamento">Data do Pagamento</Label>
          <DatePicker
            id="dataPagamento"
            onChange={(selectedDates: Date[]) => {
              if (selectedDates[0]) {
                setFormData({
                  ...formData,
                  dataPagamento: selectedDates[0]
                });
              } else {
                setFormData({
                  ...formData,
                  dataPagamento: undefined
                });
              }
            }}
            defaultDate={formData.dataPagamento ? new Date(formData.dataPagamento) : undefined}
          />
          <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
            Data em que o pagamento foi efetivado (para status aprovado manualmente)
          </p>
        </div>

        <div>
          <Label htmlFor="id_payment">ID do Pagamento</Label>
          <Input
            id="id_payment"
            type="text"
            value={formData.id_payment || ''}
            onChange={(e) => setFormData({ ...formData, id_payment: e.target.value })}
            placeholder="ID do pagamento (opcional)"
          />
        </div>

        <div>
          <Label htmlFor="gatewayId">Gateway de Pagamento</Label>
          <Select
            value={gateways.find(g => g.id === formData.gatewayId) ? 
              { value: formData.gatewayId!.toString(), label: gateways.find(g => g.id === formData.gatewayId)!.nome } : 
              undefined
            }
            onChange={(option) => {
              const selectedOption = option as SelectOption;
              setFormData({ 
                ...formData, 
                gatewayId: selectedOption ? parseInt(selectedOption.value) : undefined 
              });
            }}
            options={gateways.map(gateway => ({
              value: gateway.id.toString(),
              label: `${gateway.nome} (${gateway.gateway}) ${gateway.isDefault ? '(Padrão)' : ''}`
            }))}
            placeholder="Selecione um gateway (opcional)"
            isClearable
            isLoading={isLoading}
          />
        </div>

        <div>
          <Label htmlFor="conexaoId">Conexão WhatsApp</Label>
          <Select
            value={conexoes.find(c => c.id === formData.conexaoId) ? 
              { value: formData.conexaoId!.toString(), label: conexoes.find(c => c.id === formData.conexaoId)!.nome } : 
              undefined
            }
            onChange={(option) => {
              const selectedOption = option as SelectOption;
              setFormData({ 
                ...formData, 
                conexaoId: selectedOption ? parseInt(selectedOption.value) : undefined 
              });
            }}
            options={conexoes.map(conexao => ({
              value: conexao.id.toString(),
              label: `${conexao.nome} ${conexao.isDefault ? '(Padrão)' : ''}`
            }))}
            placeholder="Selecione uma conexão (opcional)"
            isClearable
            isLoading={isLoading}
          />
        </div>
      </div>

      {/* Dados de Pagamento Opcionais */}
      <div className="space-y-4">
        <h3 className="text-lg font-medium text-gray-900 dark:text-white">
          Dados de Pagamento (Opcionais)
        </h3>
        
        <div className="grid grid-cols-1 gap-4">
          <div>
            <Label htmlFor="copiacola">Copia e Cola</Label>
            <Input
              id="copiacola"
              type="text"
              value={formData.copiacola || ''}
              onChange={(e) => setFormData({ ...formData, copiacola: e.target.value })}
              placeholder="Código copia e cola (opcional)"
            />
          </div>

          <div>
            <Label htmlFor="qrcode">QR Code</Label>
            <Input
              id="qrcode"
              type="text"
              value={formData.qrcode || ''}
              onChange={(e) => setFormData({ ...formData, qrcode: e.target.value })}
              placeholder="QR Code (opcional)"
            />
          </div>

          <div>
            <Label htmlFor="codigobarra">Código de Barra</Label>
            <Input
              id="codigobarra"
              type="text"
              value={formData.codigobarra || ''}
              onChange={(e) => setFormData({ ...formData, codigobarra: e.target.value })}
              placeholder="Código de barra (opcional)"
            />
          </div>
        </div>
      </div>

      <div className="bg-blue-50 dark:bg-blue-900/20 p-4 rounded-lg">
        <h4 className="font-medium text-blue-900 dark:text-blue-200 mb-2">
          Informações Importantes
        </h4>
        <ul className="text-sm text-blue-800 dark:text-blue-300 space-y-1">
          <li>• Campos obrigatórios: valor, status, type, vencimento e vendaId</li>
          <li>• Os dados de pagamento são opcionais e podem ser preenchidos posteriormente</li>
          <li>• O status controla o estado atual da fatura</li>
          <li>• O gateway é opcional e pode ser usado para integração com sistemas de pagamento</li>
        </ul>
      </div>

      <div className="flex gap-3 pt-6">
        <Button
          type="submit"
          variant="primary"
          disabled={isSubmitting}
          className="flex-1"
        >
          {isSubmitting ? 'Salvando...' : vendaFinal ? 'Atualizar' : 'Criar'} Fatura
        </Button>
        {onCancel && (
          <Button
            type="button"
            variant="secondary"
            onClick={onCancel}
            disabled={isSubmitting}
          >
            Cancelar
          </Button>
        )}
      </div>
    </form>
  );
};

export default VendaFinalForm;
