import React, { useState } from 'react';
import { VendaFinal, StatusVendaFinal, TipoPagamento } from '@/services/api/venda.service';
import { formatCurrency, formatDate, getDueDateColor } from '@/utils/format';
import Button from '@/components/ui/Button';

interface VendaFinalCardProps {
  vendaFinal: VendaFinal;
  onEdit: () => void;
  onDelete: () => void;
  onSend?: () => void;
  showActions?: boolean;
}

const getStatusColor = (status: StatusVendaFinal) => {
  switch (status) {
    case 'APPROVED':
      return 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400';
    case 'PENDING':
      return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-400';
    case 'CANCELLED':
      return 'bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-400';
    default:
      return 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-400';
  }
};

const getStatusText = (status: StatusVendaFinal) => {
  switch (status) {
    case 'APPROVED':
      return 'Aprovado';
    case 'PENDING':
      return 'Pendente';
    case 'CANCELLED':
      return 'Cancelado';
    default:
      return status;
  }
};

const getTipoText = (tipo: TipoPagamento) => {
  switch (tipo) {
    case 'PIX':
      return 'PIX';
    case 'BOLETO':
      return 'Boleto';
    default:
      return tipo;
  }
};

const VendaFinalCard: React.FC<VendaFinalCardProps> = ({
  vendaFinal,
  onEdit,
  onDelete,
  onSend,
  showActions = true
}) => {
  const [showPaymentDetails, setShowPaymentDetails] = useState(false);
  const [copiedField, setCopiedField] = useState<string | null>(null);

  const copyToClipboard = (text: string, fieldName: string) => {
    navigator.clipboard.writeText(text);
    setCopiedField(fieldName);
    setTimeout(() => setCopiedField(null), 2000);
  };

  const maskSensitiveData = (data: string, visibleChars = 4) => {
    if (data.length <= visibleChars) return data;
    return data.slice(0, visibleChars) + '*'.repeat(data.length - visibleChars);
  };

  return (
    <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6 hover:shadow-lg transition-shadow duration-200">
      {/* Header */}
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
            Fatura #{vendaFinal.id}
          </h3>
          <div className="flex items-center gap-2 mb-2">
            <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(vendaFinal.status)}`}>
              {getStatusText(vendaFinal.status)}
            </span>
            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-400">
              {getTipoText(vendaFinal.type)}
            </span>
          </div>
          <div className="text-sm text-gray-500 dark:text-gray-400">
            UUID: {vendaFinal.uuid.slice(0, 8)}...
          </div>
        </div>
        <div className="flex gap-2 ml-4">

          {(showActions && onSend) && (
            <Button
              variant="primary"
              size="sm"
              onClick={onSend}
              className="p-2"
              title="Enviar fatura para o cliente"
            >
              <svg className="w-4 h-4" viewBox="0 0 20 20" fill="currentColor">
                <path d="M10.894 2.553a1 1 0 00-1.788 0l-7 14a1 1 0 001.169 1.409l5-1.429A1 1 0 009 15.571V11a1 1 0 112 0v4.571a1 1 0 00.725.962l5 1.428a1 1 0 001.17-1.408l-7-14z" />
              </svg>
            </Button>
          )}
          {showActions && (
            <Button
              variant="secondary"
              size="sm"
              onClick={onEdit}
              className="p-2"
            >
              <svg className="w-4 h-4" viewBox="0 0 20 20" fill="currentColor">
                <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
              </svg>
            </Button>
          )}
          {showActions && (
            <Button
              variant="danger"
              size="sm"
              onClick={onDelete}
              className="p-2"
            >
              <svg className="w-4 h-4" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
              </svg>
            </Button>
          )}

        </div>
      </div>

      {/* Financial Info */}
      <div className="grid grid-cols-2 gap-4 mb-4">
        <div>
          <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Valor</dt>
          <dd className="text-lg font-semibold text-gray-900 dark:text-white">
            {formatCurrency(vendaFinal.valor)}
          </dd>
        </div>
        <div>
          <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Vencimento</dt>
          <dd className={`text-sm text-gray-900 dark:text-white border-b border-b-${getDueDateColor(vendaFinal.vencimento)}-500`}>
            {formatDate(vendaFinal.vencimento)}
          </dd>
        </div>
        {vendaFinal.dataPagamento && (
          <div className="col-span-2">
            <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Data de Pagamento</dt>
            <dd className="text-sm text-green-600 dark:text-green-400 font-medium">
              {formatDate(vendaFinal.dataPagamento)}
            </dd>
          </div>
        )}
      </div>

      {/* Payment Info */}
      <div className="mb-4">
        <div className="flex items-center justify-between mb-2">
          <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300">
            Dados de Pagamento
          </h4>
          <Button
            variant="secondary"
            size="sm"
            onClick={() => setShowPaymentDetails(!showPaymentDetails)}
            className="text-xs"
          >
            {showPaymentDetails ? 'Ocultar' : 'Mostrar'}
          </Button>
        </div>

        <div className="space-y-2">
          <div>
            <span className="text-sm text-gray-500 dark:text-gray-400">ID do Pagamento:</span>
            <div className="flex items-center gap-2">
              <span className="text-sm font-mono text-gray-900 dark:text-white">
                {showPaymentDetails ? (vendaFinal.id_payment || 'N/A') : maskSensitiveData(vendaFinal.id_payment || 'N/A')}
              </span>
              {vendaFinal.id_payment && (
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => copyToClipboard(vendaFinal.id_payment!, 'id_payment')}
                  className="p-1"
                >
                  {copiedField === 'id_payment' ? (
                    <svg className="w-3 h-3 text-green-600" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                  ) : (
                    <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M8 3a1 1 0 011-1h2a1 1 0 110 2H9a1 1 0 01-1-1z" />
                      <path d="M6 3a2 2 0 00-2 2v11a2 2 0 002 2h8a2 2 0 002-2V5a2 2 0 00-2-2 3 3 0 01-3 3H9a3 3 0 01-3-3z" />
                    </svg>
                  )}
                </Button>
              )}
            </div>
          </div>

          {vendaFinal.copiacola && (
            <div>
              <span className="text-sm text-gray-500 dark:text-gray-400">Copia e Cola:</span>
              <div className="flex items-center gap-2">
                <span className="text-sm font-mono text-gray-900 dark:text-white break-all">
                  {showPaymentDetails ? vendaFinal.copiacola : maskSensitiveData(vendaFinal.copiacola, 20)}
                </span>
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => copyToClipboard(vendaFinal.copiacola!, 'copiacola')}
                  className="p-1 min-w-fit"
                >
                  {copiedField === 'copiacola' ? (
                    <svg className="w-3 h-3 text-green-600" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                  ) : (
                    <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M8 3a1 1 0 011-1h2a1 1 0 110 2H9a1 1 0 01-1-1z" />
                      <path d="M6 3a2 2 0 00-2 2v11a2 2 0 002 2h8a2 2 0 002-2V5a2 2 0 00-2-2 3 3 0 01-3 3H9a3 3 0 01-3-3z" />
                    </svg>
                  )}
                </Button>
              </div>
            </div>
          )}

          {vendaFinal.codigobarra && (
            <div>
              <span className="text-sm text-gray-500 dark:text-gray-400">Código de Barra:</span>
              <div className="flex items-center gap-2">
                <span className="text-sm font-mono text-gray-900 dark:text-white">
                  {showPaymentDetails ? vendaFinal.codigobarra : maskSensitiveData(vendaFinal.codigobarra, 10)}
                </span>
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => copyToClipboard(vendaFinal.codigobarra!, 'codigobarra')}
                  className="p-1"
                >
                  {copiedField === 'codigobarra' ? (
                    <svg className="w-3 h-3 text-green-600" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                  ) : (
                    <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M8 3a1 1 0 011-1h2a1 1 0 110 2H9a1 1 0 01-1-1z" />
                      <path d="M6 3a2 2 0 00-2 2v11a2 2 0 002 2h8a2 2 0 002-2V5a2 2 0 00-2-2 3 3 0 01-3 3H9a3 3 0 01-3-3z" />
                    </svg>
                  )}
                </Button>
              </div>
            </div>
          )}

          {/* {vendaFinal.qrcode && (
            <div>
              <span className="text-sm text-gray-500 dark:text-gray-400">QR Code:</span>
              <div className="flex items-center gap-2">
                <span className="text-sm font-mono text-gray-900 dark:text-white">
                  {showPaymentDetails ? vendaFinal.qrcode : maskSensitiveData(vendaFinal.qrcode, 15)}
                </span>
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => copyToClipboard(vendaFinal.qrcode!, 'qrcode')}
                  className="p-1"
                >
                  {copiedField === 'qrcode' ? (
                    <svg className="w-3 h-3 text-green-600" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                  ) : (
                    <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M8 3a1 1 0 011-1h2a1 1 0 110 2H9a1 1 0 01-1-1z" />
                      <path d="M6 3a2 2 0 00-2 2v11a2 2 0 002 2h8a2 2 0 002-2V5a2 2 0 00-2-2 3 3 0 01-3 3H9a3 3 0 01-3-3z" />
                    </svg>
                  )}
                </Button>
              </div>
            </div>
          )} */}
        </div>
      </div>

      {/* Footer */}
      <div className="pt-4 border-t border-gray-200 dark:border-gray-700">
        <div className="text-xs text-gray-500 dark:text-gray-400">
          Venda ID: #{vendaFinal.vendaId}
        </div>
      </div>
    </div>
  );
};

export default VendaFinalCard;
